<template>
    <div class="home">
        <!-- 扫一扫 -->
        <div class="sweep" @click="ff_kaifa">
             <van-icon class="scan" name="scan" size="20" color='#fff'/> 
        </div>
       
        <!-- 轮播图 -->
        <van-swipe :autoplay="3000" class="banner" indicator-color="#242371">
             
            <van-swipe-item class="bannerImg"  v-for="(image, index) in images" :key="index">
                 <template #default>
                    
                     <!-- 热点按钮 -->
                     <div v-if="image.isBut" :class="[ image.isStyle ? 'bules' : 'whites' ]">{{image.butText}}
                         <van-icon name="arrow"  class="arrow"/>
                     </div>
                      <img v-lazy="image.src" class="bannerImg"/>
                 </template>
            </van-swipe-item>


             
        </van-swipe>
        <!-- 第一波福利 -->
        <Onewelfare></Onewelfare>

        <!-- 第二波福利 -->
        <Twowelfare></Twowelfare>

        <!-- 推荐区 -->
        <Recomefare :Lists="recomeData"></Recomefare>

        
       <!-- 广告  -->
        <van-swipe class="finallyFare" :show-indicators=false :loop="false" :width="300" :height="160" >
            <van-swipe-item v-for="(item,index) in bannerData" :key="index">
                <template >
                    <van-image :src="item.bannerImg" height="160px" :alt="item.name" radius='15px'>
                        <template>
                            <span :class="index%2 == 0 ? 'bules' : 'whits' "  @click="ff_goPages('Details',item.pid)">
                                     立即抢购
                            </span>
                        </template>
                    </van-image>
                   
                </template>
            </van-swipe-item>
        </van-swipe>


        <!-- 商品列表 -->
        <van-tabs class="myTabs" v-model="activeName"   sticky  animated background="#f5f5f3"  line-width="25px" >
            <van-tab v-for="(item,index) in goodsData"  :title="item.name" :key="index" title-style='font-size: 13px;'>
                <ul class="comLists">
                    <li v-for="(item,index) in goodsLits" :key="index" class="coms" @click="ff_goPages('Details',item.pid)">
                          <img :src="item.smallImg"/>
                          <div>
                              <p>{{item.name}}</p>
                              <p>
                                  <span>￥{{item.price}}</span> 
                                  <span>￥{{item.original}}</span>
                              </p>
                              <span v-if="item.isTale">满减</span>
                              <p>已售卖{{item.sales | ff_Pain}}件 <span class="icon"></span></p>
                          </div>

                    </li>
                </ul>
                
            </van-tab>
            <van-tab title="更多>" title-style='font-size: 12px;' disabled  @click="ff_goPages('Menu')"></van-tab>
            
        </van-tabs>
        <!--  -->
       

    

     
    
    </div>
</template>

<script>
    import '../assets/less/home.less'
    import Onewelfare from '../components/Onewelfare.vue'
    import Twowelfare from '../components/Twowelfare.vue'
    import Recomefare from '../components/Recomefare.vue'
    export default {
        name:'Home',
        data() {
            return {
            images: [
                {
                   src     : require('../assets/img/banner07.png'),
                   butText : '立即尝鲜',
                   isBut   : true,
                   isStyle : true
                },
                {
                   src     : require('../assets/img/banner08.png'),
                   butText :  '',
                   isBut   : false,
                   isStyle : false
                },
                {
                   src     : require('../assets/img/banner09.png'),
                   butText : '立即尝鲜',
                   isBut   : true,
                   isStyle : false
                },
                {
                   src     : require('../assets/img/banner10.png'),
                   butText : '马上抢购',
                   isBut   : true,
                   isStyle : false
                },
                {
                   src     : require('../assets/img/banner11.png'),
                   butText : '马上下单',
                   isBut   : true,
                   isStyle : true
                },
                {
                   src     : require('../assets/img/banner12.png'),
                   butText : '立即充值',
                   isBut   : true,
                   isStyle : false
                },
                     

            ],
            recomeData:[],
            bannerData:[],
            activeName:'coffee',
            goodsData:[
                {
                    type : 'coffee',
                    name : '咖啡饮品'
                },
                 {
                    type : 'faddish',
                    name : '爆款周边'
                },
                 {
                    type : 'snacks',
                    name : '冲调零食'
                },
                 {
                    type : 'work',
                    name : '舒适办公'
                },
                
            ],
            goodsLits:[]
            };
        },
        created() {
            this.$toast.loading({
                        message: '加载中...',
                        // 禁止点击背景
                        forbidClick: true,
                        loadingType: 'spinner',
            });


            let keyId = this.appkey;

            // 推广专区的数据请求
            this.axios({
                url:'/typeProducts',
                method:'GET',
                params:{
                    appkey:keyId,
                    key:'isHot',
                    value:1,
                }
            })
            .then(res=>{
                res.data.result.forEach((item,index)=>{
                    if(index<4){
                         this.recomeData.push(item);
                        
                    }
                });
        
               
            })
            .catch(err=>{
                 
            })

           

            //最后的广告数据请求
            this.axios({
                url:'/banner',
                method:'GET',
                params:{
                    appkey:keyId,                    
                }
            })
            .then(res=>{
               this.bannerData = res.data.result;
              
            })
            .catch(err=>{
                 
            })

            // 加载商品
            this.ff_upload();


        },

        methods: {
            // 维护中
            ff_kaifa(){
                 this.$toast.loading({
                    message: '   维护中  ',
                    forbidClick: true,
                    loadingType: 'spinner',
                });
            },
        
            // 加载商品
            ff_upload(){
                    this.axios({
                        url:'/typeProducts',
                        method:'GET',
                        params:{
                            appkey:this.appkey,
                            key:'type',
                            value:'coffee',
                        }
                    })
                    .then(res=>{
                         this.goodsLits = res.data.result;
                         this.goodsLits.map(item=>{
                            item.sales = Math.floor(Math.random()*(7000-3000)+3000);
                            item.isTale = Math.random() >= 0.5 ? 'true' : 'false';
                            item.original = parseInt(item.price) + 20;
                        })
                        
                        
                   
                    })
                    .catch(err=>{
                         
                    })
            },

           
            // 跳转模块
            ff_goPages(name,pid=null){
                this.$router.push({name,query:{pid}});
            },
           

           

        },
        // 过滤器
        filters:{
            ff_Pain(v){
                let pain =`${v.toString().slice(0,2)}00+`;
                return pain;
            }
        },
        components:{
            Onewelfare,
            Twowelfare,
            Recomefare,
        }
        
    }
</script>

<style lang="less" scoped>

</style>